import { AnnotationQuery, DataQuery, DataSourceJsonData } from '@grafana/data';

export interface UnmigratedOracleAnnotationQuery extends AnnotationQuery<OracleQuery> {
  annotation?: OracleAnnotationQuery;
}
export interface OracleOptions extends DataSourceJsonData {
  database?: string;
  timezone_name?: string;
  use_dst?: boolean;
  user?: string;
  useTNSNamesBasedConnection?: boolean;
  useKerberosAuthentication?: boolean;
}

export interface OracleSecureOptions extends DataSourceJsonData {
  password?: string;
}

export interface OracleQuery extends DataQuery {
  format: string;
  alias: string;
  rawSql: string;
  timezone_name: string;
  use_dst: boolean;
}

export interface OracleAnnotationQuery extends DataQuery {
  rawQuery?: string;
}

export const DefaultAnnotationQuery = `SELECT
  $__timeFilter("time_column"),
  "text_column" as text,
  "tags_column" as tags
FROM
  METRIC_TABLE
WHERE
  $__timeFilter("time_column")
`;
