import React, { useState } from 'react';
// See: https://github.com/grafana/grafana/issues/26512
import {} from '@emotion/core';
import {
  DataSourcePluginOptionsEditorProps,
  onUpdateDatasourceJsonDataOption,
  onUpdateDatasourceJsonDataOptionSelect,
  onUpdateDatasourceSecureJsonDataOption,
  SelectableValue,
} from '@grafana/data';
import { InlineFormLabel, Input, Button, LegacyForms, Select } from '@grafana/ui';
import { OracleOptions, OracleSecureOptions } from '../types';
import { getAllTimezones, Timezone } from 'countries-and-timezones';

export type Props = DataSourcePluginOptionsEditorProps<OracleOptions, OracleSecureOptions>;

export const ConfigEditor = (props: Props) => {
  const getTimezonesForConfig = () => {
    const tzList: SelectableValue[] = Object.values(getAllTimezones()).map((tzEntry: Timezone) => ({
      label: `${tzEntry.name} ${tzEntry.utcOffsetStr}`,
      value: tzEntry.name,
    }));
    return tzList;
  };

  const timezones = getTimezonesForConfig();
  const { options } = props;
  const secureJsonData = (options.secureJsonData || { password: '' }) as OracleSecureOptions;
  const [useTNSNamesBasedConnection, setUseTNSNamesBasedConnection] = useState(
    options.jsonData.useTNSNamesBasedConnection || false
  );
  const [useKerberosAuthentication, setUseKerberosAuthentication] = useState(
    options.jsonData.useKerberosAuthentication || false
  );
  const passwordConfigured = options.secureJsonFields.password;
  // set a sane default
  options.jsonData.timezone_name = options.jsonData.timezone_name || 'UTC';

  const onResetPassword = () => {
    const { onOptionsChange, options } = props;
    options.secureJsonFields.password = false;
    onOptionsChange({
      ...options,
      secureJsonFields: {
        ...options.secureJsonFields,
        password: false,
      },
      secureJsonData: {
        ...options.secureJsonData,
        password: '',
      },
    });
  };

  const toggleUseTNSNames = () => {
    setUseTNSNamesBasedConnection(!useTNSNamesBasedConnection);
    options.jsonData.useTNSNamesBasedConnection = !options.jsonData.useTNSNamesBasedConnection;
    onUpdateDatasourceJsonDataOption(props, 'useTNSNamesBasedConnection');
  };
  const toggleKerberos = () => {
    setUseKerberosAuthentication(!useKerberosAuthentication);
    options.jsonData.useKerberosAuthentication = !options.jsonData.useKerberosAuthentication;
    onUpdateDatasourceJsonDataOption(props, 'useKerberosAuthentication');
  };

  return (
    <>
      <h3 className="page-heading">Oracle Connection</h3>
      <div className="gf-form-group">
        <div className="gf-form">
          <LegacyForms.Switch
            label="Enable TNSNames"
            checked={useTNSNamesBasedConnection}
            onChange={toggleUseTNSNames}
            tooltip="Allows specifying a tnsnames.ora entry for connection"
          />
        </div>

        {!useTNSNamesBasedConnection && (
          <div className="gf-form-group max-width-30">
            <div className="gf-form max-width-30">
              <InlineFormLabel className="width-9 max-width-9" tooltip="Hostname or IP Address with TCP port number">
                Host
              </InlineFormLabel>
              <Input
                defaultValue={options.url}
                placeholder="localhost:1521"
                onChange={(event: any) => {
                  options.url = event.target.value;
                }}
                required
              />
            </div>
            <div className="gf-form max-width-30">
              <InlineFormLabel className="width-9 max-width-9" tooltip="Name of Database">
                Database
              </InlineFormLabel>
              <Input
                defaultValue={options?.jsonData?.database || ''}
                placeholder="database name"
                onChange={onUpdateDatasourceJsonDataOption(props, 'database')}
                required
              />
            </div>
          </div>
        )}

        {useTNSNamesBasedConnection && (
          <div className="gf-form-group max-width-30">
            <div className="gf-form max-width-30">
              <InlineFormLabel
                className="width-9 max-width-9"
                tooltip="Use connection config specified in tnsnames.ora"
              >
                TNSNAME
              </InlineFormLabel>
              <Input
                defaultValue={options.url || ''}
                placeholder="server/DB"
                onChange={(event: any) => {
                  options.url = event.target.value;
                }}
                required
              />
            </div>
          </div>
        )}

        <h3 className="page-heading">Oracle Authentication Method</h3>
        <div className="gf-form">
          <LegacyForms.Switch
            label="Enable Kerberos Authentication"
            checked={useKerberosAuthentication}
            onChange={toggleKerberos}
            tooltip="Uses kerberos authentication instead of basic auth username and password"
          />
        </div>
        {!useKerberosAuthentication && (
          <div className="gf-form-group">
            <div className="gf-form max-width-30">
              <InlineFormLabel
                className="width-9 max-width-9"
                tooltip="An Oracle username with access to the specified database"
              >
                User
              </InlineFormLabel>
              <Input
                className="max-width-10"
                defaultValue={options?.jsonData?.user || ''}
                placeholder="user"
                onChange={onUpdateDatasourceJsonDataOption(props, 'user')}
                required
              />
            </div>
            <div className="gf-form max-width-30">
              <InlineFormLabel className="width-9 max-width-9" tooltip="NOTE: This password will be encrypted">
                Password
              </InlineFormLabel>
              {passwordConfigured ? (
                <Input className="max-width-10" placeholder="configured" disabled={true} />
              ) : (
                <Input
                  className="width-10 max-width-10"
                  value={secureJsonData.password || ''}
                  onChange={onUpdateDatasourceSecureJsonDataOption(props, 'password')}
                />
              )}
              {passwordConfigured && (
                <Button variant="secondary" type="button" onClick={onResetPassword}>
                  RESET
                </Button>
              )}
            </div>
          </div>
        )}
      </div>

      <div className="gf-form-inline">
        <div className="gf-form">
          <InlineFormLabel
            className="width-8 max-width-8"
            tooltip="Choose the default timezone of the Oracle server. Typically this is UTC."
          >
            Time Zone
          </InlineFormLabel>
          <Select
            value={options.jsonData.timezone_name}
            isSearchable={true}
            openMenuOnFocus={true}
            options={timezones}
            onChange={onUpdateDatasourceJsonDataOptionSelect(props, 'timezone_name')}
          />
        </div>
      </div>

      <div className="gf-form-group">
        <div className="grafana-info-box">
          <h5>User Permission</h5>
          <p>
            The database user should only be granted SELECT permissions on the specified database &amp; tables you want
            to query. Grafana does not validate that queries are safe so queries can contain any SQL statement. For
            example, statements like
            <code>DELETE FROM user;</code> and <code>DROP TABLE user;</code> would be executed. To protect against this
            we
            <strong>Highly</strong> recommmend you create a specific Oracle Database user with restricted permissions.
          </p>
        </div>
      </div>
    </>
  );
};

export default ConfigEditor;
