import { DataSourceWithBackend, getTemplateSrv } from '@grafana/runtime';
import { OracleQuery, OracleOptions, DefaultAnnotationQuery, UnmigratedOracleAnnotationQuery } from './types';
import {
  DataSourceInstanceSettings,
  ScopedVar,
  DataFrame,
  DataQueryRequest,
  MetricFindValue,
  AnnotationQuery,
} from '@grafana/data';
import { map } from 'lodash';

function getText(text: string, regex?: RegExp) {
  if (regex) {
    const match = regex.exec(text);
    if (match) {
      return match[1] ? match[1] : match[0];
    }
  }
  return text;
}

function dataframeToMetricFindQueryArray(frame: DataFrame, options: any): MetricFindValue[] {
  if (frame === undefined || frame.length === 0) {
    return [];
  }
  const textIndex = frame.fields.findIndex((f) => f.name === '__text');
  const valuesIndex = frame.fields.findIndex((f) => f.name === '__value');
  const regex = options.variable.regex ? new RegExp(options.variable.regex) : undefined;
  const res: MetricFindValue[] = [];
  if (textIndex !== -1 && valuesIndex !== -1) {
    const values = frame.fields[valuesIndex].values;
    const textValues = frame.fields[textIndex].values;
    for (let i = 0; i < frame.length; i++) {
      const text = getText(textValues.get(i), regex);
      const value = getText(values.get(i), regex);
      // force it to take a value
      const item = { text: text, value: value } as MetricFindValue;
      res.push(item);
    }
    return res;
  }
  for (let i = 0; i < frame.length; i++) {
    for (const { values } of frame.fields) {
      const text = getText(values.get(i), regex);
      res.push({ text: '' + text });
    }
  }
  return res;
}

export class OracleDatasource extends DataSourceWithBackend<OracleQuery, OracleOptions> {
  // Easy access for QueryEditor
  options: OracleOptions;

  constructor(instanceSettings: DataSourceInstanceSettings<OracleOptions>) {
    super(instanceSettings);
    this.options = instanceSettings.jsonData;
    this.annotations = {
      prepareAnnotation: this.prepareAnnotation,
    };
  }

  filterQuery(query: OracleQuery): boolean {
    return !(query?.hide || query.rawSql?.trim().length === 0);
  }

  interpolateVariable(value, variable) {
    if (typeof value === 'string') {
      if (variable.multi || variable.includeAll) {
        return "'" + value + "'";
      } else {
        return value;
      }
    }

    if (typeof value === 'number') {
      return value;
    }

    const quotedValues = map(value, (val) => {
      return "'" + val + "'";
    });
    return quotedValues.join(',');
  }

  applyTemplateVariables(query: OracleQuery, scopedVars: ScopedVar): any {
    if (!query.rawSql || query.hide) {
      return undefined; // Don't do anything
    }
    return {
      ...query,
      rawSql: getTemplateSrv().replace(query.rawSql, scopedVars, this.interpolateVariable),
    };
  }

  prepareAnnotation(query: UnmigratedOracleAnnotationQuery): AnnotationQuery<OracleQuery> {
    if (!query.target) {
      query.target = {
        refId: 'Anno',
        format: '',
        alias: '',
        rawSql: DefaultAnnotationQuery,
        timezone_name: '',
        use_dst: false,
      };
    }

    // We have a configured annotation query. Update the rawSql
    if (query.annotation?.rawQuery && query.annotation?.rawQuery !== query.target.rawSql) {
      query.target.rawSql = query.annotation?.rawQuery;
    }

    return query;
  }

  async metricFindQuery(request: OracleQuery, options: any): Promise<MetricFindValue[]> {
    const query = {
      targets: [
        {
          ...options,
          rawSql: request,
          refId: 'A',
          format: 'table',
          datasourceId: this.id,
        },
      ],
      range: options.range,
      rangeRaw: options.rangeRaw,
    } as DataQueryRequest<OracleQuery>;
    const results = await this.query(query).toPromise();
    return dataframeToMetricFindQueryArray(results.data[0], options);
  }
}
