import { find } from 'lodash';
import React, { PureComponent } from 'react';
import { QueryEditorProps, SelectableValue } from '@grafana/data';
import { CodeEditorSuggestionItem, CodeEditorSuggestionItemKind, CodeEditor } from '@grafana/ui';
import { OracleDatasource } from '../datasource';
import { OracleQuery, OracleOptions } from '../types';
import { getTemplateSrv } from '@grafana/runtime';

const defaultQuery = `SELECT
  $__time(time_column),
  value1
FROM
  metric_table
WHERE
  $__timeFilter(time_column)
`;

const helpBlock = `Time series:
- return column named <i>time</i> (UTC in seconds or timestamp)
- return column(s) with numeric datatype as values
- (Optional: return column named <i>metric</i> to represent the series name. If no column named metric is found the column name of the value column is used as series name)

Table:
- return any set of columns

Macros:
- $__time(column) -&gt; column as "time"
- $__timeEpoch -&gt; extract(epoch from column) as "time"
- $__timeFilter(column) -&gt;  column &ge; to_timestamp(1492750877) AND column &le; to_timestamp(1492750877)
- $__unixEpochFilter(column) -&gt;  column &gt; 1492750877 AND column &lt; 1492750877
- $__timeGroup(column,'5m') -&gt; (extract(epoch from "dateColumn")/extract(epoch from '5m'::interval))::int

Example of group by and order by with $__timeGroup:
SELECT
  $__timeGroup(date_time_col, '1h') AS time,
  sum(value) as value
FROM yourtable
GROUP BY time
ORDER BY time

Or build your own conditionals using these macros which just return the values:
- $__timeFrom() -&gt;  to_timestamp(1492750877)
- $__timeTo() -&gt;  to_timestamp(1492750877)
- $__unixEpochFrom() -&gt;  1492750877
- $__unixEpochTo() -&gt;  1492750877
`;

export type Props = QueryEditorProps<OracleDatasource, OracleQuery, OracleOptions>;

interface State {
  showHelp: boolean;
  showLastQuerySQL: boolean;
  syntaxLoaded: boolean;
  lastQueryMeta: any;
  lastQueryError: string;
}

export class QueryEditor extends PureComponent<Props, State> {
  constructor(props: Props) {
    super(props);

    this.state = {
      showHelp: false,
      showLastQuerySQL: false,
      syntaxLoaded: false,
      lastQueryMeta: null,
      lastQueryError: null,
    };

    const { query } = this.props;
    console.log(query);
    query.format = query.format || 'time_series';
    query.rawSql = query.rawSql || defaultQuery;
  }

  onChangeRunQuery = (query: OracleQuery) => {
    const { onChange, onRunQuery } = this.props;
    onChange(query);
    onRunQuery();
  };

  onPressEnter = () => {
    const { onRunQuery } = this.props;
    onRunQuery();
  };

  onShowHelp = () => {
    this.setState({
      showHelp: !this.state.showHelp,
    });
  };

  componentDidUpdate(prevProps) {
    const { data } = this.props;

    if (data) {
      const { series, error } = data;

      if (error) {
        this.onDataError(error);
      } else {
        this.onDataReceived(series);
      }
    }
  }

  onDataError = (err: any) => {
    const { query } = this.props;

    if (err.data && err.data.results) {
      const queryRes = err.data.results[query.refId];
      if (queryRes) {
        this.setState({
          lastQueryMeta: queryRes.meta,
          lastQueryError: queryRes.error,
        });
      }
    }
  };

  onDataReceived = (dataList) => {
    const { query } = this.props;

    this.setState({
      lastQueryMeta: null,
      lastQueryError: null,
    });

    const anySeriesFromQuery: any = find(dataList, {
      refId: query.refId,
    });

    if (anySeriesFromQuery) {
      this.setState({
        lastQueryMeta: anySeriesFromQuery.meta,
      });
    }
  };

  onShowLastQuerySQL = () => {
    this.setState({
      showLastQuerySQL: !this.state.showLastQuerySQL,
    });
  };

  onRawSqlChange = (sqlStr: string) => {
    const { query } = this.props;
    const nextQuery: OracleQuery = query;
    nextQuery.rawSql = sqlStr;
    this.onChangeRunQuery(nextQuery);
  };

  onResultFormatChange = (format: SelectableValue) => {
    const { query } = this.props;
    const nextQuery: OracleQuery = query;
    nextQuery.format = format.value;
    this.onChangeRunQuery(nextQuery);
  };

  getSuggestions = (): CodeEditorSuggestionItem[] => {
    // TODO: sync with the backend... what is possible?
    const sugs: CodeEditorSuggestionItem[] = [
      {
        label: '$__timeGroup',
        kind: CodeEditorSuggestionItemKind.Method,
        detail: 'Standard time group',
      },
      {
        label: `$__timeGroup(date_time_col, '1h')`,
        kind: CodeEditorSuggestionItemKind.Method,
        detail: 'time group with column',
      },
      {
        label: `$__timeFrom()`,
        kind: CodeEditorSuggestionItemKind.Method,
        detail: 'time from',
      },
      {
        label: `$__timeTo()`,
        kind: CodeEditorSuggestionItemKind.Method,
        detail: 'time to',
      },
    ];

    const templateSrv = getTemplateSrv();
    if (templateSrv) {
      templateSrv.getVariables().forEach((variable) => {
        const label = '${' + variable.name + '}';
        let val = templateSrv.replace(label);
        if (val === label) {
          val = '';
        }
        sugs.push({
          label,
          kind: CodeEditorSuggestionItemKind.Text,
          //origin: VariableOrigin.Template,
          detail: `(Template Variable) ${val}`,
        });
      });
    }

    return sugs;
  };

  render() {
    const { query } = this.props;
    const { showHelp, showLastQuerySQL, lastQueryError, lastQueryMeta } = this.state;

    return (
      <>
        <CodeEditor
          height={'250px'}
          language="sql"
          value={query.rawSql || ''}
          onBlur={this.onRawSqlChange}
          onSave={this.onRawSqlChange}
          showMiniMap={false}
          showLineNumbers={true}
          getSuggestions={this.getSuggestions}
        />
        <div className="gf-form-group">
          <div className="gf-form-inline">
            <div className="gf-form">
              <button className="gf-form-label query-keyword width-7 pointer" onClick={this.onShowHelp}>
                Show Help {showHelp ? <i className="fa fa-caret-down"></i> : <i className="fa fa-caret-right"></i>}
              </button>
            </div>
            <div className="gf-form">
              <button className="gf-form-label query-keyword width-8 pointer" onClick={this.onShowLastQuerySQL}>
                Generated SQL{' '}
                {showLastQuerySQL ? <i className="fa fa-caret-down"></i> : <i className="fa fa-caret-right"></i>}
              </button>
            </div>
            <div className="gf-form gf-form--grow">
              <div className="gf-form-label gf-form-label--grow"></div>
            </div>
          </div>

          {showLastQuerySQL && (
            <div className="gf-form">
              {lastQueryMeta?.sql && <pre className="gf-form-pre">{lastQueryMeta.sql}</pre>}
            </div>
          )}

          {showHelp && (
            <div className="gf-form" ng-show="ctrl.showHelp">
              <pre className="gf-form-pre alert alert-info">{helpBlock}</pre>
            </div>
          )}

          {lastQueryError && (
            <div className="gf-form" ng-show="ctrl.lastQueryError">
              <pre className="gf-form-pre alert alert-error">{lastQueryError}</pre>
            </div>
          )}
        </div>
      </>
    );
  }
}

export default QueryEditor;
